.Toolbar{
    height: 80px;
    width:100%;
    top:0;
    left:0;
    background-color: #1d1e22;
    display:flex;
    justify-content: space-between;
    align-items:center;
    padding: 0 20px;
    box-sizing:border-box;
    z-index: 90;
    position: absolute;
    border-bottom: 2px solid #000;

}
.Toolbar nav{
    height: 100%;
}
.Logo{
    height: 80%;
}
h1{
    color: #fff;
}
.DrawerToggle {
    width: 40px;
    height: 100%;
    display: flex;
    flex-flow: column;
    justify-content: space-around;
    align-items: center;
    padding: 10px 0;
    box-sizing: border-box;
    cursor: pointer;
}

.DrawerToggle div {
    width: 90%;
    height: 3px;
    background-color: white;
}
